import avatar from "@/components/yq-avatar/yq-avatar.vue";
import {
	api,
	postajax
} from '@/utils/api.js'

import {
	fileUrl
} from '@/utils/index.js'
export default {
	data() {
		return {
			edit: false,
			avatarUrl: '',
			userinfo: {},
		}
	},
	components: {
		avatar
	},
	onLoad() {
		this.userinfo = uni.getStorageSync('userinfo');
		console.log(this.userinfo.picUrl)
		this.avatarUrl = fileUrl + this.userinfo.picUrl
	},
	methods: {
		doBefore() {

		},
		doUpload(rsp) {
			this.avatarUrl = rsp.path;
			this.uploadImg();

		},
		clk() {
			if (this.edit) {
				this.$refs.avatar.fChooseImg('', {
					selWidth: '350upx',
					selHeight: '350upx',
					expWidth: '260upx',
					expHeight: '260upx',
					inner: 'true'
				});
			}
		},
		handle() {
			if (this.edit) {
				/* 提交数据 */
				postajax(api.updatePeronal, this.userinfo).then(da => {
					if (da.code == 0) {
						uni.showToast({
							title: '修改个人信息成功'
						});
						this.edit = !this.edit;
						uni.setStorageSync('userinfo', this.userinfo);
					}
				})
			} else {
				this.edit = !this.edit
			}
		},
		uploadImg() {
			let that = this;
			if (this.edit) {
				wx.getFileSystemManager().readFile({
					filePath: this.avatarUrl,
					encoding: 'base64',
					success: res => {
						// callBack(res.data);
						postajax(api.uploadImg, {
							file: 'data:image/png;base64,' + res.data,
							fileName: new Date().getTime() + 'avatar.png'
						}).then(da => {
							if (da.code == 0) {
								uni.showToast({
									title: '修改个人头像成功'
								})

								that.edit = !that.edit;
								that.userinfo['picUrl'] = da.picUrl;
								uni.setStorageSync('userinfo', that.userinfo);
							}
						})
					},
					fail: err => {
						console.log(err);
					}
				})

			}
		}
	}
}
